<template>
  <main>
    <h1>Private Area</h1>
    <p>
      This is your private area, enjoy! To commit your changes, just click
      <span @click="persistChanges()">here</span>.
    </p>

    <table>
      <tr>
        <th>Email Address</th>
        <td>
          <input
            type="email"
            name="email"
            v-model="uploader.email"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <button @click="changeReadable('email')">Edit</button>
        </td>
      </tr>
      <tr>
        <th>Name and Surname</th>
        <td>
          <input
            type="text"
            name="nameSurname"
            v-model="uploader.nameSurname"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <button @click="changeReadable('nameSurname')">Edit</button>
        </td>
      </tr>
      <tr>
        <th>Password</th>
        <td>
          <input
            type="password"
            name="password"
            v-model="uploader.password"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <button @click="changeReadable('password')">Edit</button>
        </td>
      </tr>
    </table>

    <form :action="logoutLink()" method="post">
      <input type="submit" value="Logout" />
    </form>

    <form :action="actionURL()" method="POST" enctype="multipart/form-data">
      <p>
        Do you want add or change your profile picture?&nbsp;
        <label for="profile_picture_file">
          Click here
          <input
            type="file"
            id="profile_picture_file"
            name="logo"
            @change="showPicture()"
          />
        </label>
      </p>
      <div>
        <img
          v-if="profile_picture !== ''"
          v-bind:src="profile_picture"
          alt="new profile picture"
        />
        <label v-if="profile_picture !== ''" for="submit_prof_pic">
          Do you really wanna set this image as profile picture?
          <input
            v-if="profile_picture !== ''"
            id="submit_prof_pic"
            type="submit"
            value="CHANGE"
          />
        </label>
      </div>
    </form>
  </main>
</template>

<script scoped>
import Axios from "axios";

export default {
  name: "PrivateArea",
  methods: {
    actionURL: function () {
      return `${process.env.VUE_APP_APIROOT}uploaderarea/logo`;
    },
    logoutLink: function () {
      return `${process.env.VUE_APP_HOST}/logout`;
    },
    showPicture: function () {
      let imageFile = document.querySelector("input[type='file']").files[0];
      let fileReader = new FileReader();
      let that = this;
      fileReader.readAsDataURL(imageFile);
      fileReader.onload = function () {
        that.profile_picture = fileReader.result;
      };
    },

    changeReadable: function (name) {
      let element = document.querySelector(`input[name='${name}']`);
      if (element.readOnly === true) {
        element.readOnly = false;
        element.className = "active";
        event.target.style.border = "2px inset";
        if (name === "password") element.type = "input";
      } else {
        element.readOnly = true;
        element.className = "inactive";
        event.target.style.border = "2px outset";
        if (name === "password") element.type = "password";
      }
    },

    persistChanges: function () {
      Axios.put(
        `${process.env.VUE_APP_APIROOT}uploaderarea/uploader`,
        this.uploader
      )
        .then(() =>alert("Changes committed correctly"))
        .catch(() => alert("something went wrong..."));
    },
  },

  data: function () {
    return {
      uploader: {},
      profile_picture: "",
    };
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/uploader`)
      .then((response) => (this.uploader = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
}

p span {
  cursor: pointer;
  color: blue;
  text-decoration: underline;
}

table {
  width: 60%;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

table tr td button {
  border: 3px outset;
}

table tr td button:focus {
  outline-width: 0;
}

table tr td button:active {
  border-style: inset;
}

form:first-of-type {
  margin-top: 5vh;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: flex-end;
}

form:first-of-type input[type="submit"] {
  background-color: black;
  border-radius: 5px;
  padding: 1% 2%;
  width: 100px;
  font-family: "Russo One";
  font-size: 110%;
  color: rgb(214, 139, 0);
}

form:first-of-type input[type="submit"]:hover {
  cursor: pointer;
}

form:last-of-type {
  margin-top: 10px;
}

form:last-of-type input[type="file"] {
  display: none;
}

form:last-of-type label[for="profile_picture_file"] {
  color: blue;
}

form:last-of-type label[for="profile_picture_file"]:hover {
  text-decoration-line: underline;
  cursor: pointer;
}

form:last-of-type div {
  margin-top: 20px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
}

form:last-of-type img {
  max-height: 40vh;
  width: 40vh;
  height: 40vh;
  display: block;
  border: 4px solid black;
  border-radius: 20vh;
}

form:last-of-type label[for="submit_prof_pic"] {
  display: block;
}

form:last-of-type input[type="submit"] {
  display: block;
  margin-top: 10px;
  margin-right: auto;
  margin-left: auto;
  width: 80%;
  height: 50px;
  font-size: 4vh;
  font-family: "Russo One";
  color: rgb(214, 139, 0);
  background-color: rgba(0, 0, 0, 0.83);
  border-radius: 5px;
}

.active {
  background-color: white;
  border: 1px solid black;
  border-radius: 5px;
}

.inactive {
  background-color: inherit;
  border: 1px solid whitesmoke;
  border-radius: 5px;
}
</style>